
.MySet .x-header {
  border-bottom: calc(0.26666667vw) solid #e5e5e5;
}
.MySet .content-box {
  padding: 0 5vw;
}
.MySet .weui-cell {
  padding: calc(5.33333333vw);
}
.MySet .card {
  border-radius: calc(2.66666667vw);
  box-shadow: 0 0 calc(2.66666667vw) rgba(0, 0, 0, 0.1);
  margin: calc(4vw) 0 !important;
}
.MySet .log-out {
  margin-top: calc(8vw);
  color: #fc6142;
  background-color: #fff;
  overflow: initial;
}
.MySet .log-out::before,
.MySet .log-out::after {
  display: block;
  border-color: #fc6142;
}



