
.SetUserInfo .x-header {
  background-color: #ffffff;
}
.SetUserInfo .x-header::after {
  content: "";
  display: block;
  height: calc(2.66666667vw);
  background-color: #efeff4;
}
.content {
  padding-left: 5vw;
  background-color: #ffffff;
}
.content .direc {
  -webkit-box-align: start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  line-height: calc(5.33333333vw);
}
.content .flexbox {
  padding: calc(4vw) 0;
  padding-right: 5vw;
  box-sizing: border-box;
  border-bottom: calc(0.26666667vw) solid #e5e5e5;
}
.content .flexbox .avatar {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}
.content .flexbox .avatar img {
  width: calc(10.66666667vw);
  height: calc(10.66666667vw);
}
.content .flexbox .weui-cell {
  padding: 0;
}
.content .flexbox .weui-cell__ft {
  text-align: left;
  color: #222222;
}
.flex-demo {
  color: #8b8b8b;
}
.content .weui-cell::before {
  display: none;
}
.vux-cell-placeholder {
  color: #777 !important;
}



