
.ReportOpinion .x-header {
  border-bottom: calc(0.26666667vw) solid #e5e5e5;
}
.ReportOpinion .content-box {
  padding: 0 5vw;
}
.ReportOpinion .edit-box {
  border-radius: calc(2.66666667vw);
  box-shadow: 0 0 calc(2.66666667vw) rgba(0, 0, 0, 0.1);
  padding: 5vw;
  box-sizing: border-box;
  margin: calc(4vw) 0 !important;
}
.ReportOpinion .vux-x-textarea {
  padding: 0;
}
.ReportOpinion .add-img {
  display: block;
  width: calc(20vw);
  height: calc(20vw);
}
.ReportOpinion .img-item {
  padding: 0;
  margin-top: calc(5.33333333vw);
}
.ReportOpinion .img-list::before,
.ReportOpinion .img-item::after {
  display: none;
}
.ReportOpinion .commit-btn {
  background-color: #c4c4c4;
  color: #fff;
}
.ReportOpinion .active {
  background-color: #fc6142;
}



