
.Release .grid {
  padding: calc(1.6vw) calc(0.8vw);
}
.Release .grid-item {
  padding: 0 calc(0.8vw);
}
.Release .weui-grids:before,
.Release .grid-item::after {
  display: none;
}
.Release .previewer-demo-img {
  width: 23vw;
  height: 23vw;
  object-fit: cover;
}
.Release .x-textarea {
  font-size: calc(3.46666667vw);
}
.Release .x-textarea::before {
  display: none;
}
.Release .x-textarea::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: calc(0.26666667vw);
  border-top: calc(0.26666667vw) solid #D9D9D9;
  color: #D9D9D9;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
  left: calc(4vw);
}
.Release .commit-btn-box {
  padding: 0 calc(4vw);
  box-sizing: border-box;
  position: fixed;
  bottom: calc(4vw);
  left: 0;
  right: 0;
  margin: auto;
}
.Release .commit-btn-box .commit-btn {
  background-color: #c4c4c4;
  color: #fff;
}
.Release .commit-btn-box .commit-btn.active {
  background-color: #fc6142;
}



