
.SelectAddress .weui-panel:before {
  display: none;
}
.SelectAddress .weui-cell {
  padding-left: 0;
  border-bottom: calc(0.26666667vw) solid #ededed;
}
.SelectAddress .vux-card-content {
  padding-left: 5vw;
  box-sizing: border-box;
}
.SelectAddress .vux-label {
  font-size: calc(3.73333333vw);
}
.SelectAddress .vux-label-desc {
  font-size: calc(2.93333333vw);
  color: #8e8e8e;
}
.SelectAddress .iconfont {
  font-size: calc(5.33333333vw);
  color: #c5c5c5 !important;
}
.SelectAddress .icon-xuanzhong {
  color: #fc6142 !important;
}
.SelectAddress .commit-btn-box {
  padding: 0 calc(4vw);
  box-sizing: border-box;
  position: fixed;
  bottom: calc(4vw);
  left: 0;
  right: 0;
  margin: auto;
}
.SelectAddress .commit-btn-box .commit-btn {
  background-color: #fc6142;
  color: #fff;
}



